
import React from 'react';
import Layout from 'antd/lib/layout';
import Menu from 'antd/lib/menu';
import '../style.css';
import { browserHistory  } from 'react-router';
import notification from 'antd/lib/notification';
import Icon from 'antd/lib/icon';
import Tooltip from 'antd/lib/tooltip';

const Header = Layout.Header;

export default class HeaderComp extends React.Component{
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        setTimeout(() => {
            this.openNotification();
        }, 500)
    }

    openNotification() {
        notification.open({
            message: '',
            description: '',
            icon: <Icon type="smile-circle" style={{ color: '#108ee9' }} />,
        });
    }

    render() {
        return (
            <Header className="header">
                <div className="logo"><img alt="logo" src="https://renhongl.github.io/images/panda.jpg"/></div>
                <Menu
                    theme="light"
                    mode="horizontal"
                    defaultSelectedKeys={[window.location.pathname]}
                    style={{ lineHeight: '64px' }}
                    onClick={(item) => {
                         if (item.key.indexOf('http') !== -1) {
                            window.location = item.key;
                         } else {
                            browserHistory.push(item.key);
                         }
                    }}
                >   
                    <Menu.Item key="https://github.com/renhongl/"><Tooltip title="Github" placement="bottom"><Icon type="github" style={{fontSize: '25px'}}/></Tooltip></Menu.Item>
                    <Menu.Item key="https://renhongl.github.io/"></Menu.Item>
                    <Menu.Item key="/view/about/"></Menu.Item>
                    <Menu.Item key='/view/download/'></Menu.Item>
                    <Menu.Item key="/view/"></Menu.Item>
                </Menu>
            </Header>
        )
    }
}



